export default {
	onLoad (q) {
		console.log(q)
		this.id = parseInt(q.id)
		// 获取举证资料
		this.getDetail()
		// 获取银行流水
		this.getBankStatemen()
	},
	data () {
		return {
			id: '',
			// 合同
			attachment: '',
			formData: {
				caseId: '',
				productName: '',
				investChannel: '',
				investMoney: '',
				investAddress: '',
				guaranteeAmount: '',
				caseRefund: '',
				caseNotRefund: '',
				remark: ''
			},
			// 是否能够编辑
			isEdit: false,
			bankStatement: []
		}
	},
	methods: {
		channelChange (evt) {
			console.log(evt)
			this.formData.investChannel = evt.detail.value
		},
		getDetail () {
			this.$JRequest('poofCaseDetail', '/' + this.id, data => {
				this.formData = data
				this.attachment = data.attachment
			})
		},
		// 获取银行流水
		getBankStatemen () {
			this.$JRequest('caseBankstatement', { caseId: this.id }, data => {
				console.log(data.list)
				this.bankStatement = data.list
			})
		}
	}
}
